/**
 * web端 - 拍卖/出售列表
 */
import { useEffect, useState, useMemo, useRef } from 'react';
import { Button, Popover } from 'antd';
import {
  FooterToolbar,
  ModalForm,
  PageContainer,
  ProDescriptions,
  ProFormText,
  ProFormTextArea,
  ProTable,
  Search,
} from '@ant-design/pro-components';
export default () => {
  const actionRef = useRef();
  const [activeKey, setActiveKey] = useState('key1');
  let [listData, setListData] = useState(
    Array.from({ length: Math.floor(Math.random() * 50) }, (_, i) => ({
      id: `A${i + 1}`,
      imgurl: 'https://blog.itpub.net/ueditor/php/upload/image/20201224/1608797539695919.jpeg',
      name: '商品A',
      price: '￥100',
      priceType: '￥100',
      endTime: Date.now() + 1000 * 60 * 60 * 24 * 3
    }))
  );
  let columns = [
    {
      title: '序号',
      valueType: 'index',
      width: 80,
    },
    {
      title: '商品',
      dataIndex: 'name',
      width: 150,
      ellipsis: true,
      search: false,
      render: (text: any, record: any) => (
        <>
          {
            <Popover content={() => {
              return (
                <div style={{ display: 'flex', alignItems: 'center' }}>
                  <img src={record.imgurl}
                    style={{
                      width: 350,
                      height: 350,
                      marginRight: 10
                    }}
                  />
                </div>
              );
            }} title="图片">
              <span style={{ cursor: 'pointer' }}>{record.name}</span>
            </Popover>
          }
        </>
      ),
    },
    {
      title: '价格',
      dataIndex: 'price',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '竞价',
      dataIndex: 'priceType',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '出价',
      dataIndex: 'priceType',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '一口价',
      dataIndex: 'priceType',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '您的出价',
      dataIndex: 'priceType',
      width: 150,
      ellipsis: true,
      search: false,
    },
  ];
  return (
    <>
      <ProTable
        headerTitle=""
        actionRef={actionRef}
        toolBarRender={false}
        search={false}
        rowKey="key"
        toolbar={{
          multipleLine: true,
          tabs: {
            items: [
              { tab: '拍卖列表', key: 'key1' },
              { tab: '出售', key: 'key2' },
              { tab: '完成', key: 'key3' },
            ],
            activeKey: activeKey,
            onChange (activeKey) {
              setActiveKey(activeKey);
              // tableRef.current?.clearDataSource();
              // formRef.current?.submit();
              // tableRef.current?.setSort();
            },
          },
        }}
        scroll={{
          y: 'calc(100vh - 360px)',
        }}
        request={(params) => {
          console.log(params);
          return Promise.resolve({
            data: listData,
            success: true,
            total: listData.length
          });
        }}
        columns={columns}
        rowSelection={{
          onChange: (_, selectedRows) => {

          },
        }}
      />
    </>
  );

};